.rangeByD-slider{
    &__base{
        position:relative;
        width:100%;
        height:6px;
        border:1px solid #1f2041;;
        border-radius:4px;
        border-radius:3px;
        display:flex;
        align-items:center;
        z-index: 0;
    }
    &__filler{
        position:absolute;
        z-index: 0;
        width: 0px;
        left:0;
        height:6px;
        background: linear-gradient(180deg, #6fcf97 0%, #66D2EA 100%);
        border-radius:3px;
        cursor:pointer;
    }
    &__runner{
        position: absolute;
        z-index: 2;
        left:-2px;
        width:14px;
        height:14px;
        border:2px solid #fff;
        background: linear-gradient(180deg, #6fcf97 0%, #66D2EA 100%);
        border-radius: 50%;
        cursor:pointer;
    }
